@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,700i&display=swap");

html {
  body {
    padding: 0;
    margin: 0;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
  }
}

.hero {
	
  
  height: 90vh;
  
	.container {
		// width: 1280px;
		// min-width: 1280px;
		//margin: 9rem auto;
    .hero-inner {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 60px;
      height: 90vh;
      .hero-content {
        width: 50%;

        
        .hero-content-inner {
          max-width: 400px;
		  text-align: left;
          color: #323232;
          margin: 0 auto;
          h1 {
            font-weight: 500;
            font-size: 32px;
            margin-bottom: 24px;
            .hero-content-line {
              margin: 0;
              height: 44px;
             
            //   .hero-content-line-inner {
            //   }
            }
          }
          p {
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
           padding-right: 48px;
            margin-bottom: 56px;
          }
          .btn-row {
            position: relative;
            &:before {
              position: absolute;
              content: "";
              width: 56px;
              height: 1px;
              background: #dfdfdf;
              top: 50%;
              left: -68px;
            }
            button {
              background: none;
              border: none;
              font-size: 12px;
              padding: 0;
              cursor: pointer;
              text-transform: uppercase;
              font-weight: 700;
              letter-spacing: 0.7px;
              display: flex;
              align-items: center;
              position: relative;
              &:focus {
                outline: none;
              }
              .arrow-icon {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 30px;
                background: #6eafc9;
                height: 48px;
                width: 48px;
                border-radius: 100px;
                margin-left: 16px;
                left: 64px;
                transform-origin: center center;
              }
            }
          }
        }
      }
      .hero-images {
		  margin-top: 70px;
        width: 80%;
        height: 90vh;
        position: relative;
        .hero-images-inner {
          .hero-image {
            position: absolute;
            
            &.girl {
				bottom: 24px;
				left: 0px;
				width: 52%;
				height: 65%;
            }
            &.boy {

				top: 0;
				right: 0px;
				width: 45%;
				height: 50%;
             
            }
            img {
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              width: 100%;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 760px){
  .hero-inner{
    flex-direction: column;
    margin: 0 2px;
  }

  .hero-content {
    width: 5%;
 

     
    .hero-content-inner {
      max-width: 500px;
  text-align: left;
      color: #323232;
      margin: 0 auto;
      h1 {
        font-weight: 500;
        
        margin-bottom: 24px;

      }
    }

    .hero-content-line {
      margin: 0;
      height: 44px;
      font-size: 20px;
      line-height: 10;
   
     
    
    }
  }
  p {
    font-size: 14px;
    line-height: 24px;
    font-weight: 300;
    padding-right: 48px;
    margin-bottom: 56px;
  }
}
  
